import styled from "styled-components";
import Link from "next/link";

const Container = styled.div`
  display: grid;
  grid-template-columns: repeat(5, 1fr);
  grid-gap: 10px;
  margin: 0 auto 100px;
  max-width: 650px;
`;

const Button = styled.div`
  border: 1px solid yellow;
  text-align: center;
`;

const Tab = ({ text, index, ...props }) => {
  // console.log(tabId);
  return <Button onClick={() => props.switchTab(index)}>{text}</Button>;
};

const TabBar = ({ tabs, activeTab, ...props }) => (
  <Container>
    {tabs.map((t, i) => {
      return (
        <Tab
          text={t.text}
          index={i}
          switchTab={props.switchTab}
          active={activeTab === i}
          key={t.text}
        />
      );
    })}
  </Container>
);

export default TabBar;
